import React from "react";
import styled from "styled-components";
import profile from "../images/profile.png"

export const Greetings = () => {
    return (
        <GreetingStyles>
            <img src={profile} alt="" className="avatar" />
            <h1>Good Evening Mikey!</h1>
        </GreetingStyles>
    )
}

const GreetingStyles = styled.div`
    height: 80px;
    display: flex;
    align-items: center;
    gap: 20px;

    h1{
        font-size: 30px;
        font-weight: 400;
    }

    .avatar {
        height: 50px;
        width: 50px;
        border-radius: 50px;
    }
`;