import React from "react";
import { createGlobalStyle } from "styled-components";

const GlobalStyles = createGlobalStyle`
:root {
    --light-grey: #f8f8fa;
    --medium-grey: #949daf;
    --light-purple: #f0f4fd;
    --purple: #554e9c;
    --orange: #ef8565;
    --green: #70e2c9;
    --blue: #96d7f4;
    --shadow: 2px 2px 2px rgba(0,0,0,0.5); 
}

html {
    box-sixing: border-box;
}

body {
    box-sizing: border-box;
    max-width: 100vw;
    min-height: 100vh;
    margin: 50px 70px;
    font-size: 12px;
}

img {
    width: 100%;
    height: auto;
}

h1, h2, h3, h4, h5, p, img {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

.purple-background {
    backgroung: var(--light-purple);
    border-radius: 10px;
}
`;

export default GlobalStyles;
