import React from "react";
import styled from "styled-components";

export const MainGraph = () => {
    return (
        <GraphArea>
            <header>
                <h1>Income</h1>
            <div className="menu">
                <p>Sort by:</p>
                <div className="dropdown">Month arrow</div>
                <img src="" alt="" className="download" />
            </div>
            </header>
            <div className="graph">
                graphic
                <div className="week x-sub">
                    Apr 01
                    Apr 02
                    Apr 03
                    Apr 04
                    Apr 05
                    Apr 06
                    Apr 07
                </div>
            </div>
            <div className="buttons">
                <a href="" className="select">1d</a>
                <a href="" className="select">1w</a>
                <a href="" className="select">1m</a>
                <a href="" className="select">3m</a>
                <a href="" className="select">1y</a>
                <a href="" className="select">all</a>
            </div>
        </GraphArea>
    )
}

const GraphArea = styled.div`
header {
    display: flex;
    justify-content: space-between;
    align-content: center;
}

p {
    color: grey;
}

h1 {
    margin-bottom: 10px;
}

.menu {
    display: flex;
    align-content: center;
    gap: 10px;
}

.week {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    font-size: 14px;
    color: grey;
}

.buttons {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 10px;
}

.select {
    color: purple;
    font-weight: bold;
    background: grey;
    border-radius: 20px;
    text-align: center;
    padding: 15px;
}

.select:active {
    background-color: orange;
    color: grey;
}

.dropdown {
    border-radius: 20px;
    box-shadow: 10px;
}

.download {
    width: 30px;
    border-radius: 50%;
    box-shadow: 10px;
}
`;